.App {
  text-align: center;
  background: black;
  color: gray;
  padding: 0;
  margin: 0;
  height: 100vh;
  font-family: "Poppins" , sans-serif;
  font-weight: 500;
  padding-top: 1rem;
  
}

p{
  margin-top: 1rem;
  letter-spacing: 4px;
}

.App-logo {
  height: 60vh;
  pointer-events: none;
  margin-top: 2rem;
}


.App-link {
  color: #61dafb;
}
.login-box{
  display: flex;
}

.login-input {
  height: 40px;
  background: rgb(32, 31, 31);
  width: 35%;
  font-family: 'Poppins', sans-serif;
  border-radius: 35px;
  border: 1px solid #DCD7D7;
  margin-top: 0.2rem;
  padding-left: 20px;
  font-size: medium;
  border: none;
  margin-top: 1rem;
  color: white;
}

a:-webkit-any-link {
color: white;
text-decoration: none;
}

.btn{
  border-radius: 35px;
  padding: 12px 40px 12px 40px;
  color: white;
  background: #464646;
  border: none;
  margin-left: -80px;
  cursor: pointer;
  letter-spacing: 4px;
}

.login-input::placeholder {
  text-align: left;
  color: gray;
  font-weight: 500;
  background: rgb(32, 31, 31);
}

.login-input:focus-visible{
  border: none;
  outline: none !important;
}

@media (max-width: 768px){


  .App-logo {
    max-height: 30vh !important;
    object-fit: contain;
    margin-top: 8rem;
    margin-bottom: 6rem;
  }

  p {
    letter-spacing: 1px;
    font-size: 12px;
  }

  .login-input {
    width: 90%;
    height: 30px;
    font-size: small;
  }

  .input-box{
    padding: 12px;
  }

  .btn {
    height: 40px;
    padding: 6px 20px;
    letter-spacing: 1px;
  } 
}